@use 'foundation' as *;

.p-intro {
  padding: prem(40) 0;
  background-color: $color-bg-light;

  @include mq(md) {
    padding: prem(32) 0;
  }
}

.p-intro__description {
  font-weight: $bold;
  font-size: prem(24);
  line-height: 1.5;
  letter-spacing: 0.02em;
  color: $color-bg-dark;
  text-align: center;
  -webkit-text-stroke: prem(4) $color-white;
  paint-order: stroke fill;

  @include mq(md) {
    font-size: prem(18);
  }
}

.p-intro__description--strong {
  font-size: prem(48);
  color: $color-accent;

  @include mq(md) {
    font-size: prem(24);
  }
}

.p-intro__description--medium {
  font-size: prem(28);

  @include mq(md) {
    font-size: prem(20);
  }
}