@use 'foundation' as *;

.p-footer {
  background-color: $color-bg-dark;
  padding: prem(80) 0 prem(40);
  margin-top: auto;

  @include mq(md) {
    padding: prem(32) 0;
  }
}

.p-footer__sns-box {
  padding-bottom: prem(48);
  display: flex;
  align-items: center;
  justify-content: end;
  gap: prem(100);
  border-bottom: 1px solid $color-white;

  @include mq(md) {
    padding-bottom: prem(32);
    justify-content: center;
    gap: prem(60);
  }
}

.p-footer__sns-box a {
  width: 100%;
  max-width: prem(40);

  @include mq(md) {
    max-width: prem(32);
  }
}

.p-footer__sns-box img {
  width: 100%;
  aspect-ratio: 1 / 1;
  object-fit: cover;
}

.p-footer__copyright {
  padding-top: prem(40);
  text-align: center;
  font-size: prem(12);
  color: $color-white;

  @include mq(md) {
    padding-top: prem(32);
  }
}