@use 'foundation' as *;

.p-company {
  padding: prem(120) 0;
  background-color: $color-white;

  @include mq(md) {
    padding: prem(60) 0;
  }
}

.p-company__heading {
  font-weight: $bold;
  font-size: prem(32);
  letter-spacing: 0.32em;
  color: #000000;
  text-align: center;

  @include mq(md) {
    font-size: prem(20);
  }
}

.p-company__content {
  margin-top: prem(80);
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  gap: prem(60);

  @include mq(md) {
    margin-top: prem(60);
    grid-template-columns: 1fr;
    gap: prem(32);
  }
}

.p-company__img {
  width: 100%;
  max-width: prem(500);
  flex-shrink: 0;

  @include mq(md) {
    margin-inline: auto;
    order: 2;
    max-width: 100%;
  }
}

.p-company__img img {
  width: 100%;
  aspect-ratio: 640 / 720;
  object-fit: cover;
  border-radius: prem(12);

  @include mq(md) {
    aspect-ratio: 4 / 3;
    object-position: bottom;
  }
}

.p-company__list {
  display: grid;
  grid-template-columns: prem(120) 1fr;
  gap: prem(32) 0;

  @include mq(md) {
    order: 1;
    grid-template-columns: prem(100) 1fr;
    gap: prem(24) 0;
  }
}

.p-company__term {
  font-weight: $bold;
  font-size: prem(18);
  line-height: 2;
  letter-spacing: 0.16em;
  color: #000000;

  @include mq(md) {
    font-size: prem(16);
  }
}

.p-company__desc {
  font-size: prem(18);
  line-height: 2;
  letter-spacing: 0.05em;
  color: #000000;

  @include mq(md) {
    font-size: prem(16);
  }
}