@use 'foundation' as *;

.p-feature {
  padding: prem(160) 0 prem(120);
  background-color: $color-bg-dark;

  @include mq(md) {
    padding: prem(80) 0 prem(60);
  }
}

.p-feature__heading {
  font-weight: $bold;
  font-size: prem(32);
  line-height: 1.5;
  letter-spacing: 0.16em;
  color: $color-gray;
  text-align: center;

  @include mq(md) {
    font-size: prem(20);
    letter-spacing: 0.08em;
  }
}

.p-feature__list {
  margin-top: prem(60);
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(3, 1fr);
  gap: prem(40);
  min-height: prem(1200);

  @include mq(md) {
    margin-top: prem(40);
    grid-template-rows: repeat(3, auto);
    min-height: 0;
    gap: prem(20);
  }
}

.p-feature__item {
  padding: prem(80) prem(60);
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  gap: prem(40);
  background-color: $color-white;
  border-radius: prem(12);
  height: 100%;
  min-height: 0;

  @include mq(md) {
    padding: prem(32) prem(20);
    grid-template-columns: 1fr;
    gap: prem(20);
    height: auto;
  }
}

.p-feature__item-img {
  margin-inline: auto;
  width: 100%;
  max-width: prem(400);
  flex-shrink: 0;
}

.p-feature__item-img img {
  width: 100%;
  aspect-ratio: 54 / 32;
  object-fit: contain;
}

.p-feature__item-body {
  width: 100%;
  min-width: 0;
}

.p-feature__item-head {
  display: flex;
  align-items: center;
  gap: prem(16);

  @include mq(md) {
    gap: prem(12);
  }
}

.p-feature__item-num {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: prem(60);
  height: prem(60);
  font-weight: $bold;
  font-size: prem(40);
  color: $color-white;
  background-color: $color-accent;
  border-radius: 50%;

  @include mq(md) {
    width: prem(40);
    height: prem(40);
    font-size: prem(28);
  }
}

.p-feature__item-title {
  font-weight: $bold;
  font-size: prem(20);
  line-height: 1.4;
  letter-spacing: 0.12em;
  color: $color-bg-dark;

  @include mq(md) {
    font-size: prem(18);
  }
}

.p-feature__item-text {
  margin-top: prem(32);
  line-height: 1.6;
  letter-spacing: 0.06em;
  color: $color-black;

  @include mq(md) {
    margin-top: prem(16);
  }
}