@use 'foundation' as *;

.p-voice {
  padding: prem(120) 0;
  background-color: $color-gray;

  @include mq(md) {
    padding: prem(60) 0;
  }
}

.p-voice__heading {
  font-weight: $bold;
  font-size: prem(32);
  line-height: 1.5;
  letter-spacing: 0.12em;
  color: #1F2933;
  text-align: center;

  @include mq(md) {
    font-size: prem(20);
  }
}

.p-voice__list {
  margin-top: prem(60);
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: prem(32);
  list-style: none;

  @include mq(md) {
    margin-top: prem(40);
    grid-template-columns: 1fr;
    gap: prem(24);
  }
}

.p-voice__item {
  padding: prem(40) prem(20);
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 0;
  background-color: $color-white;
  border-radius: prem(12);
  box-shadow: prem(4) prem(4) prem(8) rgba($color: #000000, $alpha: 0.25);
  overflow: hidden;

  @include mq(md) {
    padding: prem(32) prem(16) prem(20);
    height: auto;
  }
}

.p-voice__item-top {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-shrink: 0;
}

.p-voice__item-img {
  width: 100%;
  max-width: prem(120);
  flex-shrink: 0;

  @include mq(md) {
    max-width: prem(100);
  }
}

.p-voice__item-img img {
  width: 100%;
  aspect-ratio: 1 / 1;
  object-fit: cover;
  border-radius: 50%;
}

.p-voice__item-meta {
  margin-top: prem(16);
  font-size: prem(14);
  line-height: 2;
  letter-spacing: 0.04em;
  color: #1F2933;
  text-align: center;

  @include mq(md) {
    margin-top: prem(12);
  }
}

.p-voice__item-quote {
  margin-top: prem(24);
  padding: prem(14) prem(10);
  background-color: $color-gray;
  flex: 1;
  min-height: 0;

  @include mq(md) {
    margin-top: prem(20);

  }
}

.p-voice__item-text {
  font-weight: $medium;
  line-height: 1.8;
  letter-spacing: 0.04em;
  color: #1F2933;

  @include mq(md) {
    font-size: prem(14);
  }
}