@use 'foundation' as *;

.p-contact {
  padding: prem(120) 0;
  background-color: $color-bg-dark;

  @include mq(md) {
    padding: prem(60) 0;
  }
}

.p-contact__subtitle {
  font-family: $font-family-en;
  font-weight: $bold;
  font-size: prem(20);
  letter-spacing: 0.4em;
  color: $color-accent;
  text-align: center;
  text-transform: uppercase;

  @include mq(md) {
    font-size: prem(14);
  }
}

.p-contact__heading {
  margin-top: prem(16);
  font-weight: $bold;
  font-size: prem(32);
  line-height: 1.5;
  letter-spacing: 0.08em;
  color: $color-gray;
  text-align: center;

  @include mq(md) {
    margin-top: prem(12);
    font-size: prem(20);
  }
}

.p-contact__form {
  margin-top: prem(48);

  @include mq(md) {
    margin-top: prem(32);
  }
}

.p-contact__card {
  padding: prem(64) prem(56);
  background-color: $color-white;

  @include mq(md) {
    padding: prem(24) prem(16);
  }
}

.p-contact__row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: prem(40);

  @include mq(md) {
    grid-template-columns: 1fr;
    gap: prem(20);
  }
}

.p-contact__row+.p-contact__row {
  margin-top: prem(24);

  @include mq(md) {
    margin-top: prem(20);
  }
}

.p-contact__field+.p-contact__row,
.p-contact__row+.p-contact__field,
.p-contact__row+.p-contact__fieldset,
.p-contact__fieldset+.p-contact__field {
  margin-top: prem(24);

  @include mq(md) {
    margin-top: prem(20);
  }
}

.p-contact__row--budget-source {
  align-items: start;
}

.p-contact__label {
  display: block;
  font-size: prem(14);
  line-height: 1.5;
  letter-spacing: 0.01em;
  color: #1F2933;
}

.p-contact__required {
  margin-left: prem(2);
  color: #F00;
}

.p-contact__input,
.p-contact__select,
.p-contact__textarea {
  margin-top: prem(10);
  padding: prem(8) prem(16);
  width: 100%;
  min-width: 0;
  font-size: prem(16);
  line-height: 1.5;
  color: #1F2933;
  background-color: #D9D9D9;
  border: 1px solid #A1A1A1;
  border-radius: prem(4);
  appearance: none;

  @include mq(md) {
    padding: prem(10) prem(12);
  }
}

.p-contact__select {
  padding-right: prem(40);
  background-image: url(../images/common/select-arrow.webp);
  background-repeat: no-repeat;
  background-position: right;
  background-size: contain;
}

.p-contact__input,
.p-contact__textarea {
  appearance: auto;
}

.p-contact__input::placeholder,
.p-contact__textarea::placeholder {
  color: #B7B7B7;
}

.p-contact__textarea {
  min-height: prem(80);
  resize: vertical;
}

.p-contact__fieldset {
  border: none;
}

.p-contact__legend {
  padding: 0;
  display: block;
  font-size: prem(14);
  line-height: 1.5;
  letter-spacing: 0.01em;
  color: #1F2933;
}

.p-contact__checkbox-group {
  margin-top: prem(12);
  display: flex;
  flex-direction: column;
  gap: prem(12);
}

.p-contact__checkbox-option {
  display: flex;
  // align-items: center;
  cursor: pointer;
}

.p-contact__checkbox-option input {
  margin: 0;
  width: prem(16);
  height: prem(16);
  flex-shrink: 0;
  accent-color: $color-gray;
}

.p-contact__checkbox-option-text {
  padding-left: prem(10);
  font-size: prem(14);
  line-height: 1.5;
  letter-spacing: 0.01em;
  color: #1F2933;
}

.p-contact__privacy {
  margin-top: prem(18);
  font-size: prem(14);
  line-height: 2;
  letter-spacing: 0.02em;
  color: #000;
}

.p-contact__privacy-heading {
  font-weight: $bold;
}

.p-contact__privacy-text {
  margin: prem(12) 0;
}

.p-contact__checkbox-item {
  display: flex;
  align-items: center;
  gap: prem(10);
  cursor: pointer;

  @include mq(md) {
    align-items: flex-start;
    gap: prem(8);
  }
}

.p-contact__checkbox-item input {
  margin: 0;
  width: prem(16);
  height: prem(16);
  flex-shrink: 0;
  accent-color: $color-gray;
}

.p-contact__checkbox-text {
  font-size: prem(14);
  line-height: 2;
  letter-spacing: 0.02em;
  color: #000;

  @include mq(md) {
    line-height: 1.4;
  }
}

.p-contact__privacy-note {
  margin-top: prem(16);
  font-size: prem(14);
  line-height: 2;
  letter-spacing: 0.02em;
  color: #000;

  @include mq(md) {
    margin-top: prem(12);
  }
}

.p-contact__link {
  color: #2600E5;
}

.p-contact__submit {
  margin-top: prem(80);

  @include mq(md) {
    margin-top: prem(60);
  }
}

.p-contact__button {
  margin-inline: auto;
  padding: prem(20) prem(32);
  width: 100%;
  max-width: prem(240);
  font-weight: $bold;
  line-height: 1.5;
  letter-spacing: 0.05em;
  color: $color-white;
  background-color: #FF582E;
  border: none;
  border-radius: prem(8);
  cursor: pointer;
  display: block;
  text-align: center;
  transition: 0.3s;
}

.p-contact__button:hover {
  opacity: 0.7;
}

/* Contact Form 7 用 */
.p-contact .wpcf7-form-control-wrap {
  display: block;
  margin: 0;
  padding: 0;
}

.p-contact .wpcf7-form-control.wpcf7-not-valid {
  border-color: $color-red;
}

.p-contact .wpcf7-form-control {
  display: flex;
  flex-direction: column;
}

.p-contact .wpcf7-not-valid-tip {
  margin-top: prem(4);
  font-size: prem(12);
  color: $color-red;
}

.p-contact .wpcf7-response-output {
  line-height: 2;
  color: $color-accent;
  border-radius: prem(6);
}

.p-contact .wpcf7-mail-sent-ok {
  border-color: #22c55e;
  background-color: #f0fdf4;
  color: #166534;
}

.p-contact .wpcf7-validation-errors,
.p-contact .wpcf7-mail-sent-ng {
  border-color: $color-red;
  background-color: #fef2f2;
  color: #991b1b;
}

.p-contact .wpcf7-form-control-wrap label {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: prem(14);
  line-height: 1.5;
  letter-spacing: 0.02em;
  color: #1f2933;
}

.p-contact .wpcf7-list-item {
  margin: 0;
}

.p-contact .wpcf7-form-control-wrap label .wpcf7-list-item-label {
  margin-left: prem(10);
}

