@use 'foundation' as *;

.p-header {
  height: prem(80);
  background-color: $color-bg-dark;

  @include mq(md) {
    height: prem(60);
  }
}

.p-header__inner {
  margin-inline: auto;
  padding: 0 prem(40);
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: nowrap;
  height: inherit;

  @include mq(md) {
    padding: 0 0 0 prem(20);
  }
}

.p-header__logo {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  max-width: fit-content;
  height: inherit;
}

.p-header__logo a {
  display: flex;
  align-items: center;
  border-radius: prem(16);

  @include mq(md) {
    border-radius: prem(8);
  }
}

.p-header__logo img {
  width: 100%;
  max-width: prem(100);
  height: 100%;
  object-fit: contain;
  border-radius: prem(16);

  @include mq(md) {
    max-width: prem(80);
    border-radius: prem(8);
  }
}

.p-header__nav {
  height: inherit;
  display: flex;
  align-items: center;

  @include mq("md") {
    display: none;
  }
}

.p-header__nav-list {
  display: flex;
  height: inherit;
}

.p-header__nav-item {
  height: inherit;
}

.p-header__nav-item a {
  padding: 0 prem(16);
  display: flex;
  align-items: center;
  font-weight: $semi-bold;
  font-size: prem(16);
  color: #F3F4F6;
  text-decoration: none;
  height: inherit;
  white-space: nowrap;
}

.p-header__contact {
  margin-left: prem(24);
  display: block;
  font-weight: $medium;
  color: #F3F4F6;
  line-height: 1.6;
  letter-spacing: 0.02em;
  text-align: center;

  @include mq("md") {
    display: none;
  }
}

.p-header__hours {
  white-space: nowrap;
}

.p-header__cta {
  margin-left: prem(24);
  flex-shrink: 0;
}

.p-header__cta-button {
  border-radius: prem(999);
}

.p-header__hamburger {
  display: none;
  margin: 0;
  padding: 0;
  outline: none;
  border: none;
  position: relative;
  z-index: 999;
  width: prem(60);
  height: inherit;
  background-color: $color-accent;
  cursor: pointer;
  transition: 0.3s;

  @include mq("md") {
    display: block;
  }
}

.p-header__hamburger.is-open {
  background-color: transparent;
}

.p-header__hamburger span {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  width: prem(24);
  height: 1px;
  background-color: $color-white;
  transition: .5s;
}

.p-header__hamburger span:nth-of-type(1) {
  top: prem(-4);
}

.p-header__hamburger span:nth-of-type(2) {
  top: 0;
}

.p-header__hamburger span:nth-of-type(3) {
  top: prem(4);
}

.p-header__hamburger.is-open span:nth-of-type(1) {
  top: 0;
  transform: translateX(-50%) rotate(45deg);
}

.p-header__hamburger.is-open span:nth-of-type(2) {
  opacity: 0;
}

.p-header__hamburger.is-open span:nth-of-type(3) {
  top: prem(-2);
  transform: translateX(-50%) rotate(-45deg);
}

.p-header__drawer {
  padding: prem(120) 0;
  position: absolute;
  z-index: 900;
  top: 0;
  right: -120%;
  width: 100%;
  height: 100vh;
  background-color: rgba($color: $color-bg-dark, $alpha: .95);
  overflow-y: scroll;
  scrollbar-width: none;
  transition: 0.6s;
}

.p-header__drawer.is-open {
  right: 0;
}

.p-header__drawer::-webkit-scrollbar {
  display: none;
}

.p-header__drawer-nav {
  padding: 0 prem(20);
}

.p-header__drawer-item a {
  padding: prem(20) 0;
  display: block;
  font-size: prem(20);
  font-weight: $medium;
  letter-spacing: 0.02em;
  color: #F3F4F6;
  text-transform: uppercase;
  border-bottom: 1px solid #F3F4F6;
}

.p-header__drawer-contact {
  margin-top: prem(24);
  display: block;
  font-weight: $medium;
  color: #F3F4F6;
  letter-spacing: 0.02em;
  text-align: center;
}

.p-header__drawer-tel {
  padding: prem(20) 0;
  font-size: prem(20);
}

.p-header__drawer-hours {
  font-size: prem(16);
}