@use 'foundation' as *;

.p-problem-top {
    padding: prem(160) 0 prem(180);
    background-color: $color-white;
    clip-path: polygon(0 0, 100% 0, 100% 92%, 50% 100%, 0 92%);

    @include mq(md) {
        padding: prem(60) 0 prem(80);
        clip-path: polygon(0 0, 100% 0, 100% 98%, 50% 100%, 0 98%);
    }
}

.p-problem-top__title {
    font-weight: $bold;
    font-size: prem(32);
    letter-spacing: 0.02em;
    color: #1F2933;
    text-align: center;

    @include mq(md) {
        font-size: prem(20);
    }
}

.p-problem-top__list {
    margin-top: prem(120);
    display: grid;
    gap: prem(60);
    grid-template-columns: repeat(3, 1fr);

    @include mq(md) {
        margin-top: prem(40);
        gap: prem(32);
        grid-template-columns: repeat(1, 1fr);
    }
}

.p-problem-top__item {
    padding: prem(20);
    text-align: center;
    background-color: $color-gray;
    border-radius: prem(16);

    @include mq(md) {
        padding: prem(16);
    }
}

.p-problem-top__img {
    width: 100%;
}

.p-problem-top__img img {
    width: 100%;
    aspect-ratio: 4 / 3;
    object-fit: cover;
    border-radius: prem(8);
}

.p-problem-top__description {
    margin-top: prem(20);
    font-weight: $medium;
    font-size: prem(18);
    line-height: 1.6;
    letter-spacing: 0.08em;
    color: #1F2933;

    @include mq(md) {
        margin-top: prem(16);
        font-size: prem(16);
    }
}