@use "sass:math";

// ブレイクポイントの設定
// ＊＊＊＊＊SP・PCファーストについて、この設定を変更します＊＊＊＊＊
$break-flg: 1 !default; //0:SPファースト、1：PCファースト

$breakpoints: null;

@if $break-flg ==0 {
  $breakpoints: (
    "sm": "screen and (min-width: 400px)",
    "md": "screen and (min-width: 768px)",
    "lg": "screen and (min-width: 1000px)",
    "xl": "screen and (min-width: 1200px)",
  );
}

@else {
  $breakpoints: (
    "sm": "screen and (max-width: 399px)",
    "md": "screen and (max-width: 767px)",
    "lg": "screen and (max-width: 999px)",
    "xl": "screen and (max-width: 1199px)",
  );
}

// inner
$inner: 1200px;

// 余白
$padding-pc: 25px; // PC用余白設定
$padding-sp: 20px; // スマホ用余白設定

// フォント
$font-family-base: 'Noto Sans JP', sans-serif;
$font-family-en: "Heebo", sans-serif;

$regular: 400;
$medium: 500;
$semi-bold: 600;
$bold: 700;
$extra-bold: 800;

// ベースフォントサイズ
$base-font-size: 16px;

// あとで削除
// Color（Figma MCP 取得値）
$color-icon-tertiary: #b3b3b3; // --sds-color-icon-default-tertiary
$color-red: #e00;
// MV（メインビジュアル・斜め分割デザイン）
$color-mv-bg: #222831;
$color-mv-accent: #f28c00;


// メインカラー
$color-bg-dark: #1F2933;
$color-bg-light: #F5E9DA;

// サブカラー
$color-gray: #F3F4F6;
$color-white: #fff;
$color-black: #333;

// アクセントカラー
$color-accent: #F97316;