@use 'foundation' as *;

.p-tool {
  padding: prem(120) 0 prem(144);
  background-color: $color-bg-dark;

  @include mq(md) {
    padding: prem(60) 0 prem(84);
  }
}

.p-tool__heading {
  font-weight: $bold;
  font-size: prem(32);
  letter-spacing: 0.12em;
  color: $color-gray;
  text-align: center;

  @include mq(md) {
    font-size: prem(20);
  }
}

.p-tool__card {
  margin-top: prem(60);
  padding: prem(100) prem(80);
  background-color: $color-white;
  border-radius: prem(14);

  @include mq(md) {
    margin-top: prem(40);
    padding: prem(40) prem(16);
  }
}

.p-tool__list {
  display: grid;
  grid-template-columns: repeat(5, 1fr auto) 1fr;
  align-items: flex-start;
  justify-items: center;

  @include mq(md) {
    grid-template-columns: repeat(2, 1fr);
    gap: prem(32) prem(16);
  }
}

.p-tool__item {
  width: 100%;
  min-width: 0;

  @include mq(md) {
    min-width: 0;
  }
}

.p-tool__arrow {
  margin-top: prem(54);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 0;
  height: 0;
  border-top: prem(6) solid transparent;
  border-bottom: prem(6) solid transparent;
  border-left: prem(8) solid $color-black;

  @include mq(md) {
    display: none;
  }
}

.p-tool__item-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: prem(32);

  @include mq(md) {
    gap: prem(10);
  }
}

.p-tool__item-img {
  width: 100%;
  max-width: prem(120);
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;

  @include mq(md) {
    max-width: prem(100);
  }
}

.p-tool__item-img img {
  width: 100%;
  aspect-ratio: 1 / 1;
  object-fit: cover;
}

.p-tool__item-text {
  width: 100%;
  font-weight: $medium;
  font-size: prem(14);
  line-height: 1.5;
  letter-spacing: 0.05em;
  color: $color-bg-dark;
  text-align: center;
}