@use 'foundation' as *;

.p-problem-bottom {
    padding: prem(60) 0;

    @include mq(md) {
        padding: prem(48) 0;
    }
}

.p-problem-bottom__description {
    font-weight: $bold;
    font-size: prem(24);
    line-height: 1.5;
    letter-spacing: 0.08em;
    color: $color-white;
    text-align: center;

    @include mq(md) {
        font-size: prem(18);
    }
}

.p-problem-bottom__description:nth-child(2) {
    margin-top: prem(20);

    @include mq(md) {
        margin-top: prem(16);
    }

    .p-problem-bottom__description--accent {
        padding: 0 prem(8);
        font-size: prem(28);
        color: #111827;
        background-color: $color-white;
        border-radius: prem(4);

        @include mq(md) {
            padding: 0 prem(4);
            font-size: prem(24);
        }
    }

    .p-problem-bottom__description--month {
        font-size: prem(28);

        @include mq(md) {
            font-size: prem(24);
        }
    }

    .p-problem-bottom__description--ex-tax {
        font-size: prem(48);

        @include mq(md) {
            font-size: prem(38);
        }
    }

    .p-problem-bottom__description--in-tax {
        font-size: prem(36);

        @include mq(md) {
            font-size: prem(26);
        }
    }
}