@use 'foundation' as *;

.p-mv {
  position: relative;
  display: flex;
  align-items: center;
  height: 85svh;
  min-height: prem(600);
  overflow: hidden;
}

.p-mv__bg {
  position: absolute;
  inset: 0;
  z-index: 0;
}

.p-mv__bg img {
  width: 100%;
  height: 100%;
  object-fit: cover;

  @include mq(md) {
    object-position: right;
  }
}

.p-mv__overlay {
  position: absolute;
  inset: 0;
  z-index: 1;
  width: 100%;
  max-width: calc(1200 / 1920 * 100%);
  background-color: $color-mv-bg;
  clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);

  @include mq(md) {
    max-width: prem(600);
    background-color: rgba($color: $color-mv-bg, $alpha: 0.8);
  }
}

.p-mv__inner {
  padding-right: 35%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  @include mq(md) {
    padding-right: 15%;
  }

  @include mq(sm) {
    padding-right: initial;
  }
}

.p-mv__text-wrap {
  position: relative;
  z-index: 2;
  background-color: transparent;
  transform: skewX(-14deg);

  @include mq(md) {
    transform: skewX(-10deg);
  }

  >* {
    transform: skewX(14deg);

    @include mq(md) {
      transform: skewX(10deg);
    }
  }
}

.p-mv__top,
.p-mv__cta-text,
.p-mv__title {
  font-weight: $bold;
  font-size: prem(22);
  line-height: 1.5;
  letter-spacing: 0.02em;
  color: $color-gray;

  @include mq(md) {
    font-size: prem(18);
  }
}

.p-mv__title {
  margin-top: prem(80);
}

.p-mv__title-accent {
  display: inline-block;
  font-size: prem(36);
  font-weight: $bold;
  line-height: 1.2;
  color: $color-white;
  background-color: $color-mv-accent;
  border-radius: prem(4);

  @include mq(md) {
    font-size: prem(22);
  }
}

.p-mv__cta-text {
  margin-top: prem(80);

  @include mq(md) {
    margin-top: prem(60);

  }
}

.p-mv__cta {
  margin-top: prem(40);
}

.p-mv__cta-button {
  border-radius: prem(999);
}