@use 'foundation' as *;

.p-service {
  padding: prem(80) 0;
  background-color: $color-gray;

  @include mq(md) {
    padding: prem(60) 0;
  }
}

.p-service__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;

  @include mq(md) {
    flex-direction: column;
  }
}

.p-service__img {
  margin-left: prem(8);
  position: relative;
  width: 100%;
  max-width: prem(400);
  flex-shrink: 0;

  @include mq(md) {
    display: none;
  }

  &::before,
  &::after {
    content: "";
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    position: absolute;
    bottom: 0;
  }

  &::before {
    width: 100%;
    max-width: prem(95);
    aspect-ratio: 95 / 165;
    background-image: url(../images/common/service-character-01.webp);
    transform: translate(-36%, 48.5%);
    left: 0;
  }

  &::after {
    width: 100%;
    max-width: prem(120);
    aspect-ratio: 120 / 160;
    background-image: url(../images/common/service-character-02.webp);
    transform: translate(50%, 50%);
    right: 0;
  }
}

.p-service__img img {
  width: 100%;
  aspect-ratio: 7 / 8;
  object-fit: cover;
  border-radius: prem(16);
}

.p-service__heading {
  font-weight: $bold;
  font-size: prem(28);
  line-height: 1.8;
  letter-spacing: 0.08em;
  color: $color-bg-dark;
  text-align: center;

  @include mq(md) {
    font-size: prem(20);
  }
}

.p-service__text-box {
  margin-top: prem(100);
  padding-left: prem(40);
  padding-bottom: prem(16);
  background-image: linear-gradient(rgba($color-gray, 0.4), rgba($color-gray, 0.4)), url(../images/common/service-character-03.webp);
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: 100% 100%, prem(350);

  @include mq(md) {
    margin-top: prem(40);
    padding-left: initial;
    background-size: 100% 100%, contain;
  }
}

.p-service__title,
.p-service__text-item {
  font-weight: $bold;
  font-size: prem(18);
  line-height: 1.5;
  letter-spacing: 0.08em;
  color: #1F2933;

  @include mq(md) {
    font-size: prem(16);
  }
}

.p-service__text-list {
  margin-top: prem(40);

  @include mq(md) {
    margin-top: prem(32);
  }
}

.p-service__text-item+.p-service__text-item {
  margin-top: prem(20);

  @include mq(md) {
    margin-top: prem(18);
  }
}
