@use 'foundation' as *;

.p-plan {
  margin-top: prem(-24);
  padding: prem(120) 0;
  background-color: $color-bg-dark;
  border-radius: prem(24) prem(24) 0 0;

  @include mq(md) {
    padding: prem(60) 0;
  }
}

.p-plan__heading {
  font-weight: $bold;
  font-size: prem(32);
  line-height: 1.5;
  letter-spacing: 0.12em;
  color: $color-white;
  text-align: center;

  @include mq(md) {
    font-size: prem(20);
  }
}

.p-plan__list {
  margin-top: prem(60);
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: prem(40);

  @include mq(md) {
    margin-top: prem(40);
    grid-template-columns: 1fr;
    gap: prem(24);
  }
}

.p-plan__item {
  padding: prem(40) prem(24);
  background-color: $color-white;
  border-radius: prem(8);
  display: flex;
  flex-direction: column;
  align-items: center;

  @include mq(md) {
    padding: prem(32) prem(12) prem(12);
  }
}

.p-plan__item-icon {
  width: 100%;
  max-width: prem(100);
  overflow: hidden;
  flex-shrink: 0;

  @include mq(md) {
    max-width: prem(80);
  }
}

.p-plan__item-icon img {
  width: 100%;
  aspect-ratio: 1 / 1;
  object-fit: cover;
}


.p-plan__item-name {
  margin-top: prem(20);
  font-weight: $bold;
  line-height: 1.5;
  letter-spacing: 0.08em;
  color: #1F2933;
  text-align: center;

  @include mq(md) {
    margin-top: prem(16);
  }
}

.p-plan__item-price {
  margin-top: prem(16);
  font-weight: $bold;
  line-height: 1.5;
  letter-spacing: 0.08em;
  color: #1F2933;
  text-align: center;

  @include mq(md) {
    margin-top: prem(12);
  }
}

.p-plan__item-price--large {
  font-size: prem(24);

  @include mq(md) {
    font-size: prem(20);
  }
}

.p-plan__item-features {
  margin-top: prem(32);
  padding-left: prem(20);
  padding: prem(12) prem(20) prem(12) prem(32);
  background-color: $color-gray;
  list-style: disc;
  width: 100%;
  height: 100%;

  @include mq(md) {
    margin-top: prem(20);
    padding: prem(12) prem(10) prem(12) prem(32);
  }
}

.p-plan__item-feature {
  font-weight: $bold;
  line-height: 1.75;
  letter-spacing: 0.02em;
  color: #1F2933;
  text-align: left;

  @include mq(md) {
    font-size: prem(14);
  }
}

.p-plan__item-feature+.p-plan__item-feature {
  margin-top: prem(20);

  @include mq(md) {
    margin-top: prem(16);
  }
}

.p-plan__item-feature--note {
  list-style: none;
}

.p-plan__option-heading {
  margin-top: prem(64);
  font-weight: $bold;
  font-size: prem(28);
  line-height: 1.5;
  letter-spacing: 0.08em;
  color: $color-gray;
  text-align: left;

  @include mq(md) {
    margin-top: prem(32);
    font-size: prem(24);
  }
}

.p-plan__option-box {
  margin-top: prem(24);
  padding: prem(32) prem(24);
  background-color: $color-white;
  border-radius: prem(12);

  @include mq(md) {
    margin-top: prem(16);
    padding: prem(24) prem(16);
  }
}

.p-plan__option-list {
  padding-left: prem(20);
  list-style: disc;

  @include mq(md) {
    padding-left: prem(18);
  }
}

.p-plan__option-item {
  font-size: prem(20);
  letter-spacing: 0.08em;
  color: #1F2933;

  @include mq(md) {
    font-size: prem(16);
    line-height: 1.5;
  }
}

.p-plan__option-item+.p-plan__option-item {
  margin-top: prem(20);
}