@use 'foundation' as *;

.p-flow {
  margin-top: prem(-24);
  padding: prem(120) 0 prem(144);
  background-color: $color-gray;
  border-radius: prem(24) prem(24) 0 0;

  @include mq(md) {
    padding: prem(60) 0 prem(84);
  }
}

.p-flow__heading {
  font-weight: $bold;
  font-size: prem(32);
  line-height: 1.5;
  letter-spacing: 0.12em;
  color: #1F2933;
  text-align: center;

  @include mq(md) {
    font-size: prem(20);
  }
}

.p-flow__grid {
  margin-top: prem(100);
  display: grid;
  grid-template-columns: repeat(2, 1fr auto) 1fr;
  grid-template-rows: auto auto;
  row-gap: prem(80);
  align-items: stretch;
  justify-items: center;

  @include mq(md) {
    margin-top: prem(60);
    grid-template-columns: repeat(1, 1fr);
    gap: prem(40);
  }
}

.p-flow__grid>.p-flow__arrow {
  align-self: center;

  @include mq(md) {
    display: none;
  }
}

.p-flow__item {
  position: relative;
  padding: prem(60) prem(24) prem(48);
  width: 100%;
  height: 100%;
  max-width: prem(320);
  min-height: 0;
  background-color: $color-white;
  border-radius: prem(12);
  border: 1px solid $color-bg-dark;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-self: center;

  @include mq(md) {
    max-width: none;
    height: auto;
    padding: prem(40) prem(16) prem(32);
  }
}

.p-flow__item-badge {
  position: absolute;
  top: 0;
  left: prem(16);
  transform: translateY(-50%);
  align-self: flex-start;
  padding: prem(16) prem(20);
  font-weight: $bold;
  font-size: prem(20);
  letter-spacing: 0.12em;
  color: $color-white;
  background-color: $color-accent;
  border-radius: prem(999);

  @include mq(md) {
    font-size: prem(18);
    padding: prem(12) prem(16);
  }
}

.p-flow__item-title {
  font-weight: $bold;
  font-size: prem(18);
  line-height: 1.5;
  letter-spacing: 0.12em;
  color: #1F2933;
  text-align: center;
}

.p-flow__item-img {
  margin-top: prem(32);
  width: 100%;
  max-width: prem(100);
  flex-shrink: 0;

  @include mq(md) {
    margin-top: prem(24);
    max-width: prem(80);
  }
}

.p-flow__item-img img {
  width: 100%;
  aspect-ratio: 5 / 4;
  object-fit: contain;
}

.p-flow__item-list {
  margin-top: prem(32);
  padding-left: prem(20);
  list-style: disc;

  @include mq(md) {
    margin-top: prem(24);
    padding-left: prem(18);
  }
}

.p-flow__item-list-item {
  font-weight: $medium;
  font-size: prem(14);
  line-height: 1.5;
  letter-spacing: 0.08em;
  color: #1F2933;
}

.p-flow__item-list-item+.p-flow__item-list-item {
  margin-top: prem(20);

  @include mq(md) {
    margin-top: prem(16);

  }
}

.p-flow__arrow {
  flex-shrink: 0;
}

.p-flow__arrow--right {
  border-top: prem(14) solid transparent;
  border-bottom: prem(14) solid transparent;
  border-left: prem(12) solid $color-accent;
}